@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;400&display=swap');

.body {
    background-color: #2a2a72;
    color: #fff;
    font-family: 'Poppins', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    overflow: hidden;
    margin: 0;
}

.userForm {
    width: 100%;
    max-width: 700px;

    input {
        width: 100%;
        display: block;
        background-color: #4c2885;
        border: none;
        border-radius: 10px;
        color: #fff;
        padding: 1rem;
        margin-bottom: 2rem;
        font-family: inherit;
        font-size: 1rem;
        box-shadow: 0 5px 10px rgba(154, 160, 185, 0.05),
            0 15px 40px rgba(0, 0, 0, 0.1);
    }

    input::placeholder {
        color: #bbb;
    }

    input:focus {
        outline: none;
    }
}

.card {
    max-width: 800px;
    background-color: #4c2885;
    border-radius: 20px;
    box-shadow: 0 5px 10px rgba(154, 160, 185, 0.05),
        0 15px 40px rgba(0, 0, 0, 0.1);
    display: flex;
    padding: 3rem;
    margin: 0 1.5rem;
}

.avatar {
    border-radius: 50%;
    border: 10px solid #2a2a72;
    height: 150px;
    width: 150px;
}

.userInfo {
    color: #eee;
    margin-left: 2rem;

    h2 {
        margin-top: 0;
        margin-bottom: 10px;
    }

    ul {
        list-style-type: none;
        display: flex;
        justify-content: space-between;
        padding: 0;
        margin: 18px 0;
        max-width: 400px;

        li {
            display: flex;
            align-items: center;

            strong {
                font-size: 0.9rem;
                margin-left: 0.5rem;
            }
        }
    }
}

.repo {
    text-decoration: none;
    color: #fff;
    background-color: #212a72;
    font-size: 0.7rem;
    padding: 0.25rem 0.5rem;
    margin-right: 0.5rem;
    margin-bottom: 0.5rem;
    display: inline-block;
}

@media (max-width: 500px) {
    .card {
        flex-direction: column;
        align-items: center;
    }

    .userForm {
        max-width: 400px;
    }
}